.spinner, .error{
  width: 4rem;
  height: 4rem;

  position: relative;
  margin: 5rem auto;
}

.error {
  width: auto;
  height: auto;
  text-align: center;
  color: orangered;
}

.spinner::before, .spinner::after {
  content: '';
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background-color: rgb(51,114,177);
  opacity: 0.5;
  position: absolute;
  top: 0;
  left: 0;
  -webkit-animation: bounce 1.5s infinite ease-in-out;
          animation: bounce 1.5s infinite ease-in-out;
}

.spinner::after {
  -webkit-animation-delay: 0.75s;
          animation-delay: 0.75s;
}

@-webkit-keyframes bounce {
  0%, 100% {
    -webkit-transform: scale(0.0);
            transform: scale(0.0);
  } 50% {
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
  }
}

@keyframes bounce {
  0%, 100% {
    -webkit-transform: scale(0.0);
            transform: scale(0.0);
  } 50% {
    -webkit-transform: scale(1.0);
            transform: scale(1.0);
  }
}



